import React from 'react'

import Flex from './Flex'
import Box from './Box'

const HighlightNumber = ({ number, numberProps, children, ...props }) => {
  return (
    <Flex alignItems="center" {...props}>
      <Box
        mr="0.25em"
        style={{ WebkitTextStroke: '0.05em black' }}
        fontFamily="number"
        mb="0.25em"
        {...numberProps}
      >{number}</Box>
      <Box>{children}</Box>
    </Flex>
  )
}

export default HighlightNumber
