import React from 'react'
import Select from 'react-select'

import Flex from '../../components/Flex';
import Box from '../../components/Box';
import Input from '../../components/Input';

const filterOptions = [
  {
    name: 'places',
    label: '常見地點',
    options: [
      { label: '餐廳 / 夜市', value: 1 },
      { label: '學校', value: 2 },
      { label: '家庭 - 廚房', value: 3 },
      { label: '家庭 - 衛浴', value: 4 },
      { label: '家庭 - 居家', value: 5 },
      { label: '辦公室', value: 6 },
    ],
  },
]

const valueToOption = filterOptions.reduce((vto, field) => {
  vto[field.name] = field.options.reduce((vt, opt) => {
    vt[opt.value] = opt
    return vt
  }, {})
  return vto
}, {})

const FilterAndSearch = ({ onChange, values, setFieldValue }) => {
  return (
    <Flex py="1em">
      {filterOptions.map(({ name, label, options }) => (
        <Box key={name} width="15em" px="1em">
          <Select
            options={options}
            placeholder={label}
            value={valueToOption[name][values[name]]}
            onChange={selected => setFieldValue(name, selected && selected.value)}
            name={name}
            isClearable
            styles={{
              valueContainer: provided => ({
                ...provided,
                padding: '0.125em 0.5em',
              }),
              placeholder: provided => ({
                ...provided,
                marginLeft: '0.125em',
                marginRight: '0.125em',
              }),
              control: provided => ({
                ...provided,
                minHeight: '3em',
              }),
            }}
          />
        </Box>
      ))}
      <Box flex="1" pl="1m">
        <Input lineHeight="3em" placeholder="輸入垃圾關鍵字" value={values.search} name="search" onChange={onChange} />
      </Box>
    </Flex>
  )
}

export default FilterAndSearch
