import React, { createRef, useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react'
import { useWindowSize } from 'react-use'
import { get } from 'lodash'
import { IoTriangle } from 'react-icons/io5'
import gsap from 'gsap'
import ReactFullpage from '@fullpage/react-fullpage'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import FullpageHeight from '../../components/FullpageHeight'
import Text from '../../components/Text'
import Button from '../../components/Button'
import BackgroundImage from '../../components/BackgroundImage'
import theme, { responsive } from '../../components/ThemeProvider/theme';

import useResponsive from '../../contexts/mediaQuery/useResponsive'

import Grid from '../CataloguePage/Grid'
import useData from '../TrashPage/data/useData'
import useShowHeader from '../../contexts/header/useShowHeader';
import containerWidthContext from '../../contexts/containerWidth/context';

import trashes from './trashes.png'
import trashesHighlight from './highlight-trashes.png'
import title from './title.svg'
import titleOverlay from './title-overlay.svg'
import bubble from './bubble.svg'
import { ReactComponent as Charts } from './popup-charts.svg'
import Face from '../Face'

const TriangleDown = props => <IoTriangle style={{ transform: 'rotate(180deg)' }} {...props} />

const trashRatio = 2960 / 1468
const titleRatio = 1686.44 / 317.89
const scrollingDuration = 1

let timeline

const ScrollButton = (props) => (
  <Button
    className="scroll-btn"
    bg="white"
    rounded="full"
    fontSize={responsive('3em', '1.5em')}
    rightIcon={<TriangleDown />}
    px="1em"
    {...props}
  />
)

const HomePage = ({ history }) => {
  useShowHeader('colors.yellow')
  const { isMobile } = useResponsive()
  const heroTrashRef = useRef()
  const bubbleRef = useRef()
  const trashMountRef = useRef()
  const data = useData()
  const { containerWidth } = useContext(containerWidthContext)
  const windowSize = useWindowSize()
  // const [currentPage, setCurrentPage] = useState(0)
  const trashPx = useMemo(() => {
    if (containerWidth / trashRatio > windowSize.height * 0.6) {
      return ((containerWidth / trashRatio) - windowSize.height * 0.6) / 2
    }
    return 0
  }, [containerWidth, windowSize.height])

  const createPages = useCallback((fpApi) => [
    <Box pt={theme.headerHeight} height="100%">
      <Container pt="2em" textAlign="center" height="100%" position="relative">
        <BackgroundImage src={title} ratio={titleRatio}>
          <Box.FullAbs zIndex={2}>
            <BackgroundImage src={titleOverlay} ratio={titleRatio} />
          </Box.FullAbs>
        </BackgroundImage>
        <Box.Absolute left="-6%" top="0" width="36%" transform="translate3d(0, -15%, 0)">
          <BackgroundImage src={get(data, [1, 'img'])} ratio={850 / 624} transform="rotate(7deg)">
            <Face id="1" />
          </BackgroundImage>
        </Box.Absolute>
        <Box.Absolute bottom="4em" left="0" right="0" textAlign="center">
          <ScrollButton onClick={() => fpApi.moveSectionDown()}>往下滑走進垃圾堆 聽聽垃圾的心聲</ScrollButton>
        </Box.Absolute>
      </Container>
    </Box>,
    <>
      <Container height="100%" position="relative">
        <Box.Absolute bottom="4em" left="2em">
          <ScrollButton>往下滑看看難丟的三個等級</ScrollButton>
        </Box.Absolute>
      </Container>
    </>,
    <>
      <Container height="100%" position="relative">
        <Box.Absolute bottom="4em" right="2em">
          <ScrollButton>往下滑看看他們的同伴有誰</ScrollButton>
        </Box.Absolute>
      </Container>,
    </>,
    <Box pt={theme.headerHeight} height="100%">
      <Grid />
    </Box>,
  ], [data])
  const pageRefs = useMemo(() => createPages().map(() => createRef()), [createPages])
  useEffect(() => {
    if (timeline) {
      timeline.kill()
    }
    const trashWidth = (containerWidth - trashPx * 2)
    timeline = gsap.timeline({ defaults: { duration: scrollingDuration } })
    timeline.to(heroTrashRef.current, {
      scale: 1.5,
      left: trashWidth * 0.58,
      top: windowSize.height - (trashWidth / trashRatio),
      rotate: 360,
      duration: 0.8 * scrollingDuration,
    }, 0.2 * scrollingDuration)
    timeline.to(bubbleRef.current, {
      opacity: 1,
      duration: 0.2 * scrollingDuration,
    }, 0.8 * scrollingDuration)
    timeline.to(trashMountRef.current, { scale: 1.5, x: trashWidth * 0.66, y: '-75%' }, 0)

    timeline.to(trashMountRef.current, {
      left: `-=${trashWidth * 3}`,
    }, scrollingDuration)
    timeline.to(heroTrashRef.current, {
      left: `-=${trashWidth * 1.5}`,
    }, scrollingDuration)
    timeline.to(trashMountRef.current.querySelector('.trash-mount'), {
      opacity: 0.3,
      duration: 0.2 * scrollingDuration,
    }, scrollingDuration + 0.8 * scrollingDuration)
    timeline.to(trashMountRef.current.querySelector('.trash-chart'), {
      opacity: 1,
    }, scrollingDuration)

    createPages().forEach((p, i) => {
      if (i) {
        timeline.to(pageRefs[i - 1].current.querySelector('.scroll-btn'), {
          opacity: 0,
          duration: 0.25 * scrollingDuration,
        }, (i - 0.25) * scrollingDuration)
      }
    })
    timeline.pause()
  }, [containerWidth])

  // useEffect(() => {

  // }, [currentPage])
  return (
    <Box bg="colors.yellow">
      <Box.Fixed left="0" top="107vh" right="0" transform="translateY(-100%)" ref={trashMountRef} transformOrigin="center bottom">
        <Container px={`${trashPx}px`}>
          <Box.Relative>
            <BackgroundImage src={trashes} ratio={trashRatio} className="trash-mount" />
            <Box.FullAbs>
              <BackgroundImage src={trashesHighlight} ratio={trashRatio} />
            </Box.FullAbs>
            <Box.Absolute width="100%" left="12%" top="0" className="trash-chart" opacity="0">
              <Charts />
            </Box.Absolute>
          </Box.Relative>
        </Container>
      </Box.Fixed>
      <ReactFullpage
        sectionsColor={createPages().map(() => 'transparent')}
        licenseKey={process.env.REACT_APP_FULLPAGE_JS_KEY}
        scrollingSpeed={scrollingDuration * 1000}
        onLeave={(origin, destination) => {
          if (destination.isLast) {
            setTimeout(() => history.push('/catalogue'), scrollingDuration * 1000)
          }
          timeline.tweenTo(destination.index * scrollingDuration)
        }}
        render={({ fullpageApi }) => {
          return (
            <ReactFullpage.Wrapper>
              {createPages(fullpageApi).map((page, i) => (
                <div className="section" key={i} ref={pageRefs[i]}>
                  {page}
                </div>
              ))}
            </ReactFullpage.Wrapper>
          )
        }}
      />
      <FullpageHeight position="fixed" left="0" right="0" pointerEvents="none">
        <Container position="relative" height="100%">
          <Box.Absolute left="-6%" top="-100%" width="36%" ref={heroTrashRef}>
            <BackgroundImage src={get(data, [1, 'img'])} ratio={850 / 624} transform="rotate(7deg)">
              <Face id="1" />
            </BackgroundImage>
            <Box.Absolute width="90%" left="-55%" top="10%" ref={bubbleRef} opacity="0">
              <BackgroundImage src={bubble} ratio={1006 / 542}>
                <Box.Absolute left="4em" top="4em" right="3em">
                  <Text fontWeight="900" fontSize="1.25em">
                    欸新來的！看你穿得人模人樣<br />
                    肯定是和我們一樣超難丟，<br />
                    才被亂丟到這吧<br />
                    但要比難丟，我們可是競爭得很！
                  </Text>
                </Box.Absolute>
              </BackgroundImage>
            </Box.Absolute>
          </Box.Absolute>
          {/* <Box.Relative>
            <Box.Absolute top="2em" left="0" right="0">
              <BackgroundImage src={titleOverlay} ratio={titleRatio} />
            </Box.Absolute>
          </Box.Relative> */}
        </Container>
      </FullpageHeight>
    </Box>
  )
}

export default HomePage
