import React, {useEffect} from 'react';
import { Switch, Route, useLocation } from 'react-router-dom'

import Layout from './containers/Layout'

import HomePage from './containers/HomePage'
import TrashPage from './containers/TrashPage'
import CataloguePage from './containers/CataloguePage'
import GamePage from './containers/GamePage'
import PlayGame from './containers/GamePage/PlayGame'

function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}

function App() {
  return (
    <Layout>
      <ScrollToTop />
      <Switch>
        <Route path="/" exact component={HomePage} />
        <Route path="/catalogue" component={CataloguePage} />
        <Route path="/trash/:id" component={TrashPage} />
        <Route path="/game/play" component={PlayGame} />
        <Route path="/game" component={GamePage} />
      </Switch>
    </Layout>

  );
}

export default App;
